import React from 'react';
import PlayerPropListData from "../data/PlayerPropListData";
import { ListItem, ListItemIcon, ListItemText, Paper } from '@mui/material';

interface Props {
    id : number;
    data : PlayerPropListData;
    state : 'COLLAPSED' | 'EXPANDED'
}

const BetListItem: React.FC<Props> = (props) => {
    if (props.state === 'COLLAPSED') {
        return (
            <div>
              <ListItem key={props.id} >
                  <ListItemText primary={props.data.name}></ListItemText>
              </ListItem>
            </div>
          );
    } else {
        return ( <div> </div> );
    }

};

export default BetListItem;
  
  